___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  font-size: calc(0.05vw + 16px);
}

nav {
  padding: 2vh 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2vh;
}
.temp-switch-container {
  display: flex;
  align-items: center;
  gap: 1vw;
}
.temp-switch-container > div {
  font-size: 2rem;
}
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: orange;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
/* input:checked + .slider {
  background-color: #2196F3;
} */
input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.slider.round {
  border-radius: 34px;
}
.slider.round:before {
  border-radius: 50%;
}

main {
  display: flex;
  justify-content: center;
}

#hourly-weather-container {
  width: 98vw;
  height: 10vw;
  display: flex;
  overflow-x: auto;
  border: 4px solid black;
}

.weather-block {
  height: 100%;
  aspect-ratio: 1 / 1;
  box-sizing: border-box;
  border: 1px black solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.time {
  font-size: 0.9rem;
}

.icon {
  width: 50%;
  aspect-ratio: 1 / 1;
  display: block;
  margin: auto;
}

.temp {
  font-size: 1.1rem;
}
